@use '../../styles/colors' as colors;
.reviews-container {
  background-color: #1e1d1d;
  padding: 40px 0;
}

.reviews-heading-text {
  font-weight: normal;
  margin-left: 30px;
}

.all-reviews-rectangles {
  display: flex;
  flex-direction: row;
  justify-content: start;
  padding: 40px 40px 20px 40px;
}

.student-photo {
  background-image: url(../../assets/images/web_dev.png);
  background-size: 100%;
  border: 5px solid #5b1573;
  width: 235px;
  height: 235px;
  min-width: 235px;
  min-height: 235px;
  box-shadow: 0 0 5px #a71dd8;
}
.reviews-text {
  color: #a71dd8;
  padding-left: 40px;
  font-size: 30px;
}
.reviews-text-2 {
  color: #fff;
  font-size: 20px;
}
.reviews-slider-wrapper{
  .slick-dots {
    bottom: -20px;
  }
}
@media (screen and max-width: 600px) {
  .all-reviews-rectangles {
    flex-direction: column;
    padding: 5px;
    margin: 40px 0;
  }
  .student-photo {
    width: 90vw;
    height: 90vw;
    margin: auto;
  }
}
